html.dark, body.dark {
  background-color: #020D0C !important;
  color: #D91414 !important;
}

.dark .app {
  background-color: #020D0C !important;
}

.dark .card {
  background-color: #F27830 !important;
  box-shadow: 0 4px 8px rgba(242, 120, 48, 0.2) !important;
  color: #D91414 !important;
}

.dark .header,
.dark .contact footer {
  color: #D91414 !important;
}

.dark button {
  background-color: #8C0303 !important;
  color: #D91414 !important;
}

.dark input,
.dark textarea {
  background-color: #590202 !important;
  border-color: #8C0303 !important;
  color: #D91414 !important;
}

.dark input:focus,
.dark textarea:focus {
  outline: none !important;
  border: 2px solid #D91414 !important;
}

.dark h1,
.dark h2,
.dark h3,
.dark p,
.dark li,
.dark a,
.dark span {
  color: #D91414 !important;
}

.dark .toggle-switch-bg {
  background-color: #D91414 !important;
}

.dark .dot {
  background-color: #8E1616 !important;
}

.dark * {
  background-color: transparent !important;
}

.dark *:not(.card, .header, .contact footer, button, input, textarea, .toggle-switch-bg, .dot) {
  background-color: #020D0C !important;
}

.dark .u-shape {
  background-color: transparent !important;
  -webkit-background-color: transparent !important;
  -moz-background-color: transparent !important;
  mix-blend-mode: lighten !important;
}

.dark .hero-section {
  position: relative;
  min-height: 120vh;
  overflow: hidden;
}

.dark .checkbox-wrapper-5 .check {
  --size: 40px;
  position: relative;
  background: linear-gradient(90deg, #f19af3, #f099b5);
  line-height: 0;
  perspective: 400px;
  font-size: var(--size);
}

.dark .checkbox-wrapper-5 .check input[type="checkbox"],
  .dark .checkbox-wrapper-5 .check label,
  .dark .checkbox-wrapper-5 .check label::before,
  .dark .checkbox-wrapper-5 .check label::after,
  .dark .checkbox-wrapper-5 .check {
  appearance: none;
  display: inline-block;
  border-radius: var(--size);
  border: 0;
  transition: .35s ease-in-out;
  box-sizing: border-box;
  cursor: pointer;
}

.dark .checkbox-wrapper-5 .check label {
  width: calc(2.2 * var(--size));
  height: var(--size);
  background: #d7d7d7;
  overflow: hidden;
}

.dark .checkbox-wrapper-5 .check input[type="checkbox"] {
  position: absolute;
  z-index: 1;
  width: calc(.8 * var(--size));
  height: calc(.8 * var(--size));
  top: calc(.1 * var(--size));
  left: calc(.1 * var(--size));
  background: linear-gradient(45deg, #dedede, #ffffff);
  box-shadow: 0 6px 7px rgba(0,0,0,0.3);
  outline: none;
  margin: 0;
}

.dark .checkbox-wrapper-5 .check input[type="checkbox"]:checked {
  left: calc(1.3 * var(--size));
}

.dark .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label {
  background: transparent;
}

.dark .checkbox-wrapper-5 .check label::before,
  .dark .checkbox-wrapper-5 .check label::after {
  content: "· ·";
  position: absolute;
  overflow: hidden;
  left: calc(.15 * var(--size));
  top: calc(.5 * var(--size));
  height: var(--size);
  letter-spacing: calc(-0.04 * var(--size));
  color: #9b9b9b;
  font-family: "Times New Roman", serif;
  z-index: 2;
  font-size: calc(.6 * var(--size));
  border-radius: 0;
  transform-origin: 0 0 calc(-0.5 * var(--size));
  backface-visibility: hidden;
}

.dark .checkbox-wrapper-5 .check label::after {
  content: "●";
  top: calc(.65 * var(--size));
  left: calc(.2 * var(--size));
  height: calc(.1 * var(--size));
  width: calc(.35 * var(--size));
  font-size: calc(.2 * var(--size));
  transform-origin: 0 0 calc(-0.4 * var(--size));
}

.dark .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::before,
  .dark .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::after {
  left: calc(1.55 * var(--size));
  top: calc(.4 * var(--size));
  line-height: calc(.1 * var(--size));
  transform: rotateY(360deg);
}

.dark .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::after {
  height: calc(.16 * var(--size));
  top: calc(.55 * var(--size));
  left: calc(1.6 * var(--size));
  font-size: calc(.6 * var(--size));
  line-height: 0;
}