html.light, body.light {
  background-color: #FBFADA !important;
  color: #12372A !important;
}

.light .app {
  background-color: #FBFADA !important;
}

.light .card {
  background-color: #ADBC9F !important;
  box-shadow: 0 4px 8px rgba(18, 55, 42, 0.2) !important;
  color: #12372A !important;
}

.light .header,
.light .contact footer {
  color: #FBFADA !important;
}

.light button {
  background-color: #436850 !important;
  color: #FBFADA !important;
}

.light input,
.light textarea {
  background-color: #ADBC9F !important;
  border-color: #436850 !important;
  color: #12372A !important;
}

.light input:focus,
.light textarea:focus {
  outline: none !important;
  border: 2px solid #436850 !important;
}

.light h1,
.light h2,
.light h3,
.light p,
.light li,
.light a,
.light span {
  color: #12372A !important;
}

.light .toggle-switch-bg {
  background-color: #12372A !important;
}

.light .dot {
  background-color: #436850 !important;
}

.light * {
  background-color: transparent !important;
}

.light *:not(.card, .header, .contact footer, button, input, textarea, .toggle-switch-bg, .dot) {
  background-color: #FBFADA !important;
}

.light .u-shape {
  background-color: transparent !important;
  -webkit-background-color: transparent !important;
  -moz-background-color: transparent !important;
  mix-blend-mode: lighten !important;
}

.light .hero-section {
  position: relative;
  min-height: 120vh;
  overflow: hidden;
}

.light .checkbox-wrapper-5 .check {
  --size: 40px;
  position: relative;
  background: linear-gradient(90deg, #f19af3, #f099b5);
  line-height: 0;
  perspective: 400px;
  font-size: var(--size);
}

.light .checkbox-wrapper-5 .check input[type="checkbox"],
  .light .checkbox-wrapper-5 .check label,
  .light .checkbox-wrapper-5 .check label::before,
  .light .checkbox-wrapper-5 .check label::after,
  .light .checkbox-wrapper-5 .check {
  appearance: none;
  display: inline-block;
  border-radius: var(--size);
  border: 0;
  transition: .35s ease-in-out;
  box-sizing: border-box;
  cursor: pointer;
}

.light .checkbox-wrapper-5 .check label {
  width: calc(2.2 * var(--size));
  height: var(--size);
  background: #d7d7d7;
  overflow: hidden;
}

.light .checkbox-wrapper-5 .check input[type="checkbox"] {
  position: absolute;
  z-index: 1;
  width: calc(.8 * var(--size));
  height: calc(.8 * var(--size));
  top: calc(.1 * var(--size));
  left: calc(.1 * var(--size));
  background: linear-gradient(45deg, #dedede, #ffffff);
  box-shadow: 0 6px 7px rgba(0,0,0,0.3);
  outline: none;
  margin: 0;
}

.light .checkbox-wrapper-5 .check input[type="checkbox"]:checked {
  left: calc(1.3 * var(--size));
}

.light .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label {
  background: transparent;
}

.light .checkbox-wrapper-5 .check label::before,
  .light .checkbox-wrapper-5 .check label::after {
  content: "· ·";
  position: absolute;
  overflow: hidden;
  left: calc(.15 * var(--size));
  top: calc(.5 * var(--size));
  height: var(--size);
  letter-spacing: calc(-0.04 * var(--size));
  color: #9b9b9b;
  font-family: "Times New Roman", serif;
  z-index: 2;
  font-size: calc(.6 * var(--size));
  border-radius: 0;
  transform-origin: 0 0 calc(-0.5 * var(--size));
  backface-visibility: hidden;
}

.light .checkbox-wrapper-5 .check label::after {
  content: "●";
  top: calc(.65 * var(--size));
  left: calc(.2 * var(--size));
  height: calc(.1 * var(--size));
  width: calc(.35 * var(--size));
  font-size: calc(.2 * var(--size));
  transform-origin: 0 0 calc(-0.4 * var(--size));
}

.light .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::before,
  .light .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::after {
  left: calc(1.55 * var(--size));
  top: calc(.4 * var(--size));
  line-height: calc(.1 * var(--size));
  transform: rotateY(360deg);
}

.light .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::after {
  height: calc(.16 * var(--size));
  top: calc(.55 * var(--size));
  left: calc(1.6 * var(--size));
  font-size: calc(.6 * var(--size));
  line-height: 0;
}