@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 3s, color 3s;
}

.app {
  min-height: 100vh;
  background-color: inherit;
}

@media (max-width: 768px) {
  .container {
    padding: 10px;
  }
}

/* Toggle Switch Custom Animation */
.toggle-switch {
  transition: border-color 0.4s, background-color 0.4s;
}

.toggle-switch-bg {
  transition: background-color 0.4s;
}

html.dark .toggle-switch {
  border: 2px solid #8E1616 !important;
}

html.dark .toggle-switch-bg {
  background-color: #590202 !important;
}

html.light .toggle-switch {
  border: 2px solid #12372A !important;
}

html.light .toggle-switch-bg {
  background-color: #ADBC9F !important;
}

/* Custom toggle button styles from Uiverse.io */
.checkbox-wrapper-5 .check {
  --size: 40px;
  position: relative;
  background: #12372A;
  line-height: 0;
  perspective: 400px;
  font-size: var(--size);
}

html.dark .checkbox-wrapper-5 .check {
  background: #D91414;
}

.checkbox-wrapper-5 .check input[type="checkbox"],
  .checkbox-wrapper-5 .check label,
  .checkbox-wrapper-5 .check label::before,
  .checkbox-wrapper-5 .check label::after,
  .checkbox-wrapper-5 .check {
  appearance: none;
  display: inline-block;
  border-radius: var(--size);
  border: 0;
  transition: .35s ease-in-out;
  box-sizing: border-box;
  cursor: pointer;
}

.checkbox-wrapper-5 .check label {
  width: calc(2.2 * var(--size));
  height: var(--size);
  background: #d7d7d7;
  overflow: hidden;
}

.checkbox-wrapper-5 .check input[type="checkbox"] {
  position: absolute;
  z-index: 1;
  width: calc(.8 * var(--size));
  height: calc(.8 * var(--size));
  top: calc(.1 * var(--size));
  left: calc(.1 * var(--size));
  background: #fff;
  box-shadow: 0 6px 7px rgba(0,0,0,0.3);
  outline: none;
  margin: 0;
}

html.dark .checkbox-wrapper-5 .check input[type="checkbox"] {
  background: linear-gradient(45deg, #dedede, #ffffff);
}

.checkbox-wrapper-5 .check input[type="checkbox"]:checked {
  left: calc(1.3 * var(--size));
}

.checkbox-wrapper-5 .check input[type="checkbox"]:checked + label {
  background: transparent;
}

.checkbox-wrapper-5 .check label::before,
  .checkbox-wrapper-5 .check label::after {
  content: "· ·";
  position: absolute;
  overflow: hidden;
  left: calc(.15 * var(--size));
  top: calc(.5 * var(--size));
  height: var(--size);
  letter-spacing: calc(-0.04 * var(--size));
  color: #9b9b9b;
  font-family: "Times New Roman", serif;
  z-index: 2;
  font-size: calc(.6 * var(--size));
  border-radius: 0;
  transform-origin: 0 0 calc(-0.5 * var(--size));
  backface-visibility: hidden;
}

.checkbox-wrapper-5 .check label::after {
  content: "●";
  top: calc(.65 * var(--size));
  left: calc(.2 * var(--size));
  height: calc(.1 * var(--size));
  width: calc(.35 * var(--size));
  font-size: calc(.2 * var(--size));
  transform-origin: 0 0 calc(-0.4 * var(--size));
}

.checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::before,
  .checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::after {
  left: calc(1.55 * var(--size));
  top: calc(.4 * var(--size));
  line-height: calc(.1 * var(--size));
  transform: rotateY(360deg);
}

.checkbox-wrapper-5 .check input[type="checkbox"]:checked + label::after {
  height: calc(.16 * var(--size));
  top: calc(.55 * var(--size));
  left: calc(1.6 * var(--size));
  font-size: calc(.6 * var(--size));
  line-height: 0;
}

/* Dot color for light mode */
.checkbox-wrapper-5 .check input[type="checkbox"] {
  background: #436850;
}

html.dark .checkbox-wrapper-5 .check input[type="checkbox"] {
  background: linear-gradient(45deg, #dedede, #ffffff);
}